.root {
    position: absolute;
    inset-block-end: 5px;
    inset-inline-end: 5px;

    z-index: var(--ym-z-index-overlay);
}

.panel {
    display: flex;
    position: absolute;
    flex-direction: column;

    border: 1px solid var(--ym-outline-color-primary-disabled);
    border-radius: var(--ym-radius-size-m);
    padding: 5px;

    width: 290px;
    overflow: hidden;
    gap: 5px;
    inset-block-end: 40px;
    inset-inline-end: 0;

    backdrop-filter: blur(10px);
}

.group {
    display: flex;
    flex-direction: column;

    border: 1px solid var(--ym-outline-color-primary-disabled);
    border-radius: var(--ym-radius-size-m);
    padding: 5px;
    gap: 5px;
}

.block {
    margin-inline-start: 0;
    margin-inline-end: 0;
}
