.root {
    display: flex;
    flex-direction: column;

    width: 100%;
    gap: var(--ym-spacer-size-xs);
}

.cover {
    aspect-ratio: 1;

    height: auto;

    &_round {
        border-radius: var(--ym-radius-size-round);
    }
}

.infoContainer {
    display: flex;
    flex-direction: column;
    gap: var(--ym-spacer-size-xxxs);

    min-height: calc(3 * var(--ym-font-line-height-label-s));

    &_centered {
        align-items: center;
    }
}

.title {
    border-radius: var(--ym-radius-size-xxxl);

    width: 75%;
    height: 18px;
}
