@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';

.root {
    display: flex;
    align-items: center;

    border-radius: 70px;

    padding-block-start: var(--ym-spacer-size-s);
    padding-block-end: var(--ym-spacer-size-s);
    padding-inline-start: var(--ym-spacer-size-s);
    padding-inline-end: var(--ym-spacer-size-xxl);

    gap: var(--ym-spacer-size-xxs);
}

.image {
    border-radius: var(--ym-radius-size-round);

    width: 36px;
    max-width: 36px;

    &:not(:first-child) {
        margin-inline-start: calc(-1 * var(--ym-spacer-size-s));
    }

    @include aspectRatio(1, 1);
}

.tab {
    &_size {
        &_s {
            border-radius: 50px;

            padding-block-start: var(--ym-spacer-size-xxs);
            padding-block-end: var(--ym-spacer-size-xxs);
            padding-inline-start: var(--ym-spacer-size-xxs);
            padding-inline-end: var(--ym-spacer-size-l);

            .image {
                width: 32px;
                max-width: 32px;
            }
        }

        &_m {
            border-radius: 50px;

            padding-block-start: var(--ym-spacer-size-xs);
            padding-block-end: var(--ym-spacer-size-xs);
            padding-inline-start: var(--ym-spacer-size-l);
            padding-inline-end: var(--ym-spacer-size-l);
        }

        &_l {
            border-radius: 60px;

            padding-block-start: var(--ym-spacer-size-s);
            padding-block-end: var(--ym-spacer-size-s);
            padding-inline-start: var(--ym-spacer-size-xs);
            padding-inline-end: var(--ym-spacer-size-xxl);

            gap: var(--ym-spacer-size-s);

            .image {
                width: 44px;
                max-width: 44px;
            }
        }
    }

    &_withCovers {
        min-height: 58px;
    }
}

.covers {
    display: flex;
    align-items: center;
}

.description {
    max-width: 180px;

    text-align: start;
}

.title {
    text-align: inherit;

    color: var(--ym-controls-color-primary-text-enabled_variant);
}

.subtitle {
    text-align: inherit;

    color: var(--ym-controls-color-primary-text-enabled);
}
