@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/firefoxStyles.scss';

.root {
    &:hover {
        .controls {
            opacity: 1;
        }
    }
}

.content {
    padding-inline-start: var(--ym-spacer-size-l);
    scroll-padding-inline-start: var(--ym-spacer-size-l);

    scrollbar-gutter: auto;

    @include firefoxStyles() {
        padding-inline-end: var(--ym-spacer-size-l);
    }

    @include mediaBreakpointUp($ym-breakpoint-s, true) {
        margin-inline-end: calc(var(--ym-spacer-size-xs) * -1);
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs) {
        padding-inline-start: var(--ym-spacer-size-xs);
        scroll-padding-inline-start: var(--ym-spacer-size-xs);
    }
}

.item {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;

    &_columns {
        &_one {
            width: 100%;

            @include mediaBreakpointUp($ym-breakpoint-m, true) {
                width: 50%;
            }
        }

        &_two {
            max-width: calc(50% - var(--ym-spacer-size-xxl) / 2);

            @include mediaBreakpointUp($ym-breakpoint-s, true) {
                flex: 1;
            }

            @include mediaBreakpointDown($ym-breakpoint-s) {
                width: 90%;
                max-width: 90%;
            }
        }
    }
}

.trackColumn {
    display: flex;
    flex-direction: column;
}
