@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';

.root {
    display: flex;
    align-items: center;

    column-gap: var(--ym-spacer-size-xs);

    &:not(:last-child) {
        margin-inline-end: var(--ym-spacer-size-xxl);
    }

    &_withCovers {
        min-height: 60px;
    }
}

.covers {
    display: flex;
    align-items: center;
}

.cover {
    &:not(:first-child) {
        margin-inline-start: calc(-1 * var(--ym-spacer-size-m));
    }

    @include aspectRatio(1, 1);
}

.lines {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

    row-gap: var(--ym-spacer-size-xxxs);
}

.title {
    height: var(--ym-font-size-label-m);
}

.subtitle {
    height: var(--ym-font-size-label-xs);
}
