.root {
    &_liked {
        animation: liked 0.4s ease forwards;

        @media screen and (prefers-reduced-motion: reduce) {
            animation: none;
        }
    }

    &_unliked {
        animation: unliked 0.5s ease forwards;

        @media screen and (prefers-reduced-motion: reduce) {
            animation: none;
        }
    }
}

@keyframes liked {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.1);
    }

    100% {
        transform: scale(1);
    }
}

@keyframes unliked {
    0% {
        transform: translateX(-1px);
    }

    50% {
        transform: translateX(1px);
    }

    100% {
        transform: translateX(0);
    }
}
