.root {
}

.link {
    color: var(--ym-controls-color-secondary-on_default-enabled);

    &:hover,
    &:focus-visible {
        color: var(--ym-controls-color-primary-default-hovered);
    }
}

.title {
    max-width: 195px;
}

.message {
    display: flex;
    align-items: center;

    column-gap: var(--ym-spacer-size-xs);
}

.text {
    display: flex;
    flex-wrap: wrap;

    align-items: center;

    color: var(--ym-controls-color-secondary-on_default-enabled_variant);
}

.cover,
.image {
    width: 100%;
    max-width: var(--ym-icon-size-m);
    height: 100%;
    max-height: var(--ym-icon-size-m);
}
