@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/scrollbar.scss';

.root {
    display: flex;

    overflow-x: auto;
    gap: var(--ym-spacer-size-xs);
    scroll-snap-type: x mandatory;

    padding-block-start: var(--ym-spacer-size-xxxs);
    padding-block-end: var(--ym-spacer-size-xxxs);
    padding-inline-start: var(--ym-spacer-size-xs);
    padding-inline-end: var(--ym-spacer-size-xs);

    @include setScrollbarVisibility($visible: false);
}
