.icon {
    animation: 1500ms animate-pop forwards, 1500ms 1000ms heartbeat infinite;

    @media screen and (prefers-reduced-motion: reduce) {
        animation: none;
    }
}

@keyframes animate-pop {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes heartbeat {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.2);
    }

    100% {
        transform: scale(1);
    }
}
