@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    display: flex;
    justify-content: space-between;

    column-gap: var(--ym-spacer-size-xxxxl);
    row-gap: var(--ym-spacer-size-l);

    width: 100%;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
    }
}

.links {
    display: flex;
    flex-direction: column;
    gap: var(--ym-spacer-size-xs);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        gap: var(--ym-spacer-size-xxl);
    }
}

.list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    column-gap: var(--ym-spacer-size-l);

    margin: 0;
    padding: 0;

    list-style: none;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;

        row-gap: var(--ym-spacer-size-l);
    }
}

.copyrights {
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        align-items: flex-start;
        justify-content: flex-start;
    }
}

.link {
    color: var(--ym-controls-color-primary-text-enabled_variant);
}

.copyrightLink {
    color: var(--ym-controls-color-primary-text-enabled);
}

.link,
.copyrightLink {
    display: flex;

    white-space: nowrap;

    &:hover,
    &:focus-visible {
        color: var(--ym-controls-color-primary-text-hovered);
    }
}

.yandexMusicLink {
    display: inline;
}

.explicitText {
    max-width: 500px;

    color: var(--ym-controls-color-primary-text-enabled);
}

.text {
    white-space: nowrap;
    color: var(--ym-controls-color-primary-text-enabled);
}
