@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    display: flex;
    align-items: center;
    justify-content: space-between;

    padding-block-start: var(--ym-spacer-size-m);
    padding-block-end: var(--ym-spacer-size-l);
    padding-inline-start: var(--ym-spacer-size-xxl);

    @include mediaBreakpointDown($ym-breakpoint-xxs) {
        padding-block-start: var(--ym-spacer-size-l);
        padding-block-end: var(--ym-spacer-size-xs);
    }
}

.titleIcon {
    --ym-icon-padding: 5px 7px 3px 7px;

    flex-shrink: 0;

    fill: var(--ym-controls-color-primary-text-enabled);

    will-change: transform;
    transition: transform var(--ym-duration-transition);

    color: var(--ym-controls-color-primary-text-enabled);

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.start {
    display: flex;
    gap: var(--ym-spacer-size-m);
}

.coverContainer {
    flex-grow: 0;

    width: 58px;
    height: 58px;
}

.textContainer {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
}

.title {
    display: flex;
    align-items: center;
    gap: var(--ym-spacer-size-xxxs);

    &:hover {
        .titleIcon {
            transform: translateX(var(--ym-spacer-size-xs));
        }
    }
}

.description {
    color: var(--ym-controls-color-primary-text-enabled);
}

.heading {
    color: var(--ym-controls-color-primary-text-enabled_variant);
}
