@import '~@yandex-music-int/sol/mixins/supportHasSelector.scss';
@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    display: flex;
    align-items: center;
    gap: var(--ym-spacer-size-s);

    will-change: opacity;

    transition: opacity var(--ym-duration-transition);

    opacity: 0;

    @include mediaBreakpointDown($ym-breakpoint-xxs) {
        display: none;
    }

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.control {
    flex-shrink: 0;

    color: var(--ym-controls-color-primary-on_outline-disabled);

    &:hover,
    &:focus-visible {
        color: var(--ym-controls-color-primary-on_outline-enabled);
    }
}
