@import '~@yandex-music-int/sol/mixins/scrollbar';
@import '~@yandex-music-int/sol/mixins/breakpoints';

.root {
    padding-inline-start: var(--ym-spacer-size-xxxl);

    height: 100%;

    &_scrolling {
        > div {
            @include setScrollbarVisibility($visible: true);
        }
    }

    &_notScrolling {
        > div {
            @include setScrollbarVisibility($visible: false);
        }
    }

    @include mediaBreakpointDown($width: 1024) {
        padding-inline-start: var(--ym-spacer-size-l);
    }
}

.scroller {
    // Используем псевдоэлементы и задаем flex, чтобы внутри div-обертки virtuoso можно было использовать flex-grow: 1.
    // Это необходимо для того, чтобы футер занимал правильную позицию внизу родительского контейнера.
    &_withFooter {
        & > div:first-child {
            display: flex;
            flex-direction: column;
        }

        & > div:first-child > div:last-child {
            display: flex;
            flex-direction: column;

            flex-grow: 1;

            justify-content: flex-end;
        }
    }
}
