@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/calculateCarouselItemWidth.scss';
@import '~@yandex-music-int/sol/mixins/firefoxStyles.scss';

.root {
    &:hover {
        .controls {
            opacity: 1;
        }
    }
}

.content {
    padding-inline-start: var(--ym-spacer-size-xxl);
    scroll-padding-inline-start: var(--ym-spacer-size-xxl);

    scrollbar-gutter: auto;

    @include firefoxStyles() {
        padding-inline-end: var(--ym-spacer-size-l);
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs) {
        padding-inline-start: var(--ym-spacer-size-l);
        scroll-padding-inline-start: var(--ym-spacer-size-l);
    }
}

.shimmerTitle {
    --carousel-block-shimmer-title-width: 150px;
    --carousel-block-shimmer-title-height: 30px;

    margin-block-end: var(--ym-spacer-size-xxl);

    width: var(--carousel-block-shimmer-title-width);
    height: var(--carousel-block-shimmer-title-height);
}

.item {
    width: 70%;

    @include mediaBreakpointDown(1240) {
        width: 20%;
    }

    @include mediaBreakpointDown(980) {
        width: 20%;
    }

    @include mediaBreakpointDown(950) {
        width: 26%;
    }

    @include mediaBreakpointDown(920) {
        width: 28%;
    }

    @include mediaBreakpointDown(767) {
        width: 30%;
    }

    @include mediaBreakpointDown(650) {
        width: 35%;
    }

    @include mediaBreakpointDown(570) {
        width: 40%;
    }

    @include mediaBreakpointDown(500) {
        width: 47%;
    }

    @include mediaBreakpointDown(450) {
        width: 52%;
    }

    @include mediaBreakpointDown(410) {
        width: 58%;
    }

    @include mediaBreakpointDown(380) {
        width: 65%;
    }

    @include mediaBreakpointDown(340) {
        width: 70%;
    }

    @include calculateCarouselItemWidth(1240, 5, false);
    @include calculateCarouselItemWidth(1400, 6, false);
    @include calculateCarouselItemWidth(1690, 7, false);
    @include calculateCarouselItemWidth(1920, 8, false);
    @include calculateCarouselItemWidth(2150, 9, false);
    @include calculateCarouselItemWidth(2560, 10, false);
}
