@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    display: flex;
    align-items: center;

    box-shadow: 0 var(--ym-spacer-size-xxxs) var(--ym-spacer-size-m) 0 var(--ym-shadow-s);
    border: 1px solid var(--ym-outline-color-primary-disabled);
    background-color: var(--ym-controls-color-secondary-outline-selected);

    padding: var(--ym-spacer-size-xs);

    min-width: 500px;
    min-height: calc(var(--ym-icon-size-m) + var(--ym-spacer-size-xs) * 2);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        margin: auto;

        min-width: unset;
        max-width: 90%;
    }
}

.message {
    flex-grow: 1;
}

.controls {
    display: flex;
    flex-shrink: 0;

    align-items: center;

    margin-inline-start: var(--ym-spacer-size-m);
}

.icon {
    color: var(--ym-controls-color-secondary-on_default-enabled_variant);

    &:hover,
    &:focus-visible {
        color: var(--ym-controls-color-secondary-on_default-enabled);
    }
}
