@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';

.root {
    display: flex;
    align-items: center;
    justify-content: space-between;

    padding-inline-start: var(--ym-spacer-size-xs);
    padding-inline-end: var(--ym-spacer-size-xs);

    width: 100%;
}

.infoContainer {
    display: flex;
    align-items: center;
    gap: var(--ym-spacer-size-m);

    width: 70%;
}

.textContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;

    width: 100%;
}

.title {
    width: 40%;
    height: 18px;
}

.cover {
    width: 40px;

    @include aspectRatio(1, 1);
}

.action {
    width: 10%;
    height: 18px;
}
